 class='logoutBtn' @click=\"logout()\">LOG OUT</button>\n            <button class=\"loginBtn\" :style=\"{ 'opacity': !password ? '0.50' : '1' }\" :disabled=\"!password\"\n                @click=\"login()\">LOGIN</button>\n        </div>\n    </div>\n</template>\n\n<style scoped>\n.mailInfo {\n    font-style: normal;\n    font-weight: 500;\n    font-size: 14px;\n    color: #212121;\n    letter-spacing: 0px;\n    font-family: 'Roboto';\n    opacity: 1;\n    margin-top: 12px;\n    margin-left: 20px;\n    margin-right: 20px;\n}\n\n.expiredText {\n    font-style: normal;\n    font-weight: 400;\n    font-size: 14px;\n    color: #141414;\n    letter-spacing: 0px;\n    opacity: 1;\n    margin-left: 20px;\n    margin-right: 20px;\n    margin-top: 12px;\n    font-family: 'ROBOTO';\n}\n\n.expiredInput {\n    background: #FFFFFF 0% 0% no-repeat padding-box;\n    border: 1px solid #A1A1A1;\n    border-radius: 4px;\n    opacity: 1;\n    width: 400px;\n    margin-left: 20px;\n    margin-right: 20px;\n    height: 40px;\n    outline: none;\n}\n\n.buttonGroupDiv {\n    text-align: end;\n    height: 72px;\n    margin-top: 29px;\n    margin-right: 13px;\n    padding-top: 16px;\n}\n\n.loginBtn {\n    height: 40px;\n    border-radius: 4px;\n    font-size: 12px;\n    letter-spacing: 0.01px;\n    opacity: 1;\n    margin-right: 8px;\n    background-color: #F3CA40;\n    color: #212121;\n    font-family: 'ROBOTO';\n    border: unset;\n    width: 97px;\n}\n\n.logoutBtn {\n    height: 40px;\n    border-radius: 4px;\n    background-color: white;\n    font-size: 12px;\n    letter-spacing: 0.01px;\n    color: #212121;\n    opacity: 1;\n    font-family: 'ROBOTO';\n    border: unset;\n    width: 97px;\n}\n</style>"],"sourceRoot":""}]);
